body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  color: #333;
  font-size: 12px;
  background: #fffbf5;
  text-align: center;
}

.droid-sans {
  font-family: 'Droid Sans Mono', monospace !important;
}

.container {
  width: 100%;
}

textarea {
  color: grey;
  width: 50%;
}

input[type=text] {
  width: 28px;
}

h1 {
  background: white;
  border-bottom: 1px solid #f5b143;
  padding: 10px;
  margin: 0;
  text-align: center;
  font-size: 18px;
}
h1 span {
  font-size: 24px;
}

h2 {
  background: white;
  border-bottom: 1px solid #f5b143;
  border-top: 1px solid #f5b143;
  margin: 0;
  padding: 6px 0;
  text-align: center;
}

h2 span {
  font-size: 24px;
}

a {
  color: grey;
}

.rows {
  font-size: 8px;
  padding-bottom: 0;
}

.recharts-wrapper {
  margin: 20px auto;
}

.recharts-legend-wrapper {
  margin: 0 0 5px;
}

.input,
.output {
  padding: 10px;
}

footer {
  border-top: 1px solid #f5b143;
  background: white;
  padding: 6px 0;
}



